import React, { Component } from "react";
import axios from "axios";
import ReactTable from "react-table";
import "react-table/react-table.css";
import "../App.css";
export default class PastRecord extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
      loading: true,
    };
  }

  componentDidMount() {
    let apiUrl =
      "https://mj3a9u0swa.execute-api.ap-south-1.amazonaws.com/dev/dataanalysis?UserName=" +
      this.props.name;
    fetch(apiUrl)
      .then((res) => res.json())
      .then((result) => {
        let res = result.body;
        for (var i = 0; i < res.data.length; i++) {
          var date = new Date(res.data[i]["Date"] * 1000);
          console.log(date);
          var month = date.toLocaleString("default", { month: "long" });
          var day = date.getDate();
          var year = date.getFullYear();
          var fullDate = day + " " + month + ", " + year;
          res.data[i]["Date"] = fullDate;
        }
        this.setState({ loading: false, users: res.data });
      });
  }
  render() {
    const columns = [
      {
        Header: "Date",
        accessor: "Date",
      },
      {
        Header: "Production-planned",
        accessor: "Planned.Production",
      },

      {
        Header: "production-actual",
        accessor: "Actual.Production",
      },
      {
        Header: "Core - planned",
        accessor: "Planned.Core",
      },
      {
        Header: "Core - actual",
        accessor: "Actual.Core",
      },

      {
        Header: "Notes",
        accessor: "Notes",
      },
    ];
    if (this.state.loading) return <div>Please Wait data is being loading</div>;
    else return <ReactTable data={this.state.users} columns={columns} />;
  }
}
